.app-wrapper{
    display: grid;
    width: 1200px;
    margin: 0 auto;
    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
   /* grid-gap: 10px;*/
}

.app-wrapper-content{
    grid-area: c;
    background-color: rgba(255, 249, 192, 0.66);
    width: 100%;
}